@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700");
:root {
  --fontFamily: "Poppins", sans-serif;
  --mainColor: #8f6B29;
  --whiteColor: #9d9d9e;
  --blackColor: #b9b9b9;
  --transition: .5s;
}

canvas {
  position: absolute;
  width: 100%;
  height: 100%;
}

.panel-collapse.collapse.in.ng-star-inserted {
  background: #151414;
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000000;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--fontFamily);
  background: radial-gradient(ellipse at bottom, #080b0f 0%, #000000 100%) !important;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0;
  font-weight: 400;
  color: var(--blackColor);
  line-height: 1;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}
ul li {
  list-style: none;
}

:focus {
  outline: 0 !important;
}

#stars {
  content: "e";
  animation: animStar 50s linear infinite;
}

