/*==============================
Preloader CSS
==============================*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  background: var(--whiteColor);
  z-index: 99999;
}
.preloader .folding-cube {
  left: 0;
  width: 60px;
  height: 60px;
  position: absolute;
  transform: rotateZ(45deg);
  top: 50%;
  right: 0;
  margin: -60px auto 0;
}
.preloader .folding-cube .cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  transform: scale(1.1);
}
.preloader .folding-cube .cube::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--mainColor);
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  transform-origin: 100% 100%;
}
.preloader .folding-cube .cube2 {
  transform: scale(1.1) rotateZ(90deg);
}
.preloader .folding-cube .cube2::before {
  animation-delay: 0.3s;
}
.preloader .folding-cube .cube3 {
  transform: scale(1.1) rotateZ(180deg);
}
.preloader .folding-cube .cube3::before {
  animation-delay: 0.6s;
}
.preloader .folding-cube .cube4 {
  transform: scale(1.1) rotateZ(270deg);
}
.preloader .folding-cube .cube4::before {
  animation-delay: 0.9s;
}

@keyframes sk-foldCubeAngle {
  0%, 10% {
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
/*==============================
Main Header CSS
==============================*/
.navbar {
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  position: fixed !important;
  background: transparent !important;
  padding-top: 30px;
  padding-bottom: 30px;
}
.navbar.sticky-box-shadow {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  position: fixed !important;
  background: radial-gradient(ellipse at bottom, #080b0f 0%, #000000 100%) !important;
  box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
  animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
  padding-top: 10px;
  padding-bottom: 0px;
}

.navbar-light .navbar-brand {
  color: #ffffff;
  text-transform: uppercase;
  line-height: 10px;
  transition: var(--transition);
  font-size: 23px;
  font-weight: 600;
}
.navbar-light .navbar-brand span {
  color: var(--mainColor);
  line-height: 10px;
  text-transform: uppercase;
  transition: var(--transition);
  font-size: 40px;
  font-weight: 700;
}
.navbar-light .navbar-brand:focus, .navbar-light .navbar-brand:hover {
  color: var(--whiteColor);
}
.navbar-light .navbar-nav .nav-item {
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 0;
  padding-bottom: 0;
}
.navbar-light .navbar-nav .nav-item:last-child {
  padding-right: 0;
}
.navbar-light .navbar-nav .nav-item:first-child {
  padding-left: 0;
}
.navbar-light .navbar-nav .nav-item .nav-link {
  color: #b9b9b9;
  position: relative;
  cursor: pointer;
  font-weight: 500;
  transition: var(--transition);
  padding-right: 0;
  padding-left: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.navbar-light .navbar-nav .nav-item .nav-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--mainColor);
  transition: var(--transition);
  transform: scaleX(0);
}
.navbar-light .navbar-nav .nav-item .nav-link.active, .navbar-light .navbar-nav .nav-item .nav-link:hover, .navbar-light .navbar-nav .nav-item .nav-link:focus {
  color: var(--mainColor);
}
.navbar-light .navbar-nav .nav-item .nav-link.active::before, .navbar-light .navbar-nav .nav-item .nav-link:hover::before, .navbar-light .navbar-nav .nav-item .nav-link:focus::before {
  transform: scaleX(1);
}
.navbar-light .navbar-nav.for-responsive {
  display: none;
}
.navbar-light.sticky-box-shadow .navbar-brand {
  color: var(--blackColor);
}
.navbar-light.sticky-box-shadow .navbar-nav .nav-link {
  color: var(--blackColor);
}
.navbar-light.sticky-box-shadow .navbar-nav .nav-link.active, .navbar-light.sticky-box-shadow .navbar-nav .nav-link.show, .navbar-light.sticky-box-shadow .navbar-nav .nav-link:hover, .navbar-light.sticky-box-shadow .navbar-nav .nav-link:focus {
  color: var(--mainColor);
}
.navbar-light.sticky-box-shadow .navbar-nav .active .nav-link, .navbar-light.sticky-box-shadow .navbar-nav .show .nav-link {
  color: var(--mainColor);
}

@media only screen and (max-width: 991px) {
  .navbar {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .navbar-light .navbar-nav {
    background-color: var(--whiteColor);
    padding: 10px 20px;
    margin-top: 10px;
    display: none;
  }
  .navbar-light .navbar-nav .nav-item {
    padding-left: 0;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .navbar-light .navbar-nav .nav-item .nav-link {
    color: var(--blackColor);
    display: inline-block;
  }
  .navbar-light .navbar-nav .nav-item .nav-link.active, .navbar-light .navbar-nav .nav-item .nav-link:hover {
    color: var(--mainColor);
  }
  .navbar-light .navbar-nav.for-responsive {
    display: block !important;
  }
  .navbar-light .navbar-toggler {
    color: var(--whiteColor);
    font-size: inherit;
    box-shadow: unset;
    border: none;
    padding: 0;
  }
