  overflow: hidden;
  text-align: center;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
.page-title-area::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--blackColor);
  opacity: 0.6;
}

.page-title-content {
  position: relative;
  z-index: 1;
  margin-top: 70px;
}
.page-title-content h1 {
  color: var(--whiteColor);
  margin-bottom: 35px;
  font-size: 45px;
  font-weight: 600;
}
.page-title-content ul li {
  display: inline-block;
  color: var(--whiteColor);
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
}
.page-title-content ul li.active {
  color: #cccccc;
}
.page-title-content ul li a {
  color: var(--whiteColor);
}
.page-title-content ul li a:hover {
  color: var(--mainColor);
}

/*==============================
Blog Details Area CSS
==============================*/
.blog-details-area {
  background-image: url(assets/img/pattern.png);
  background-attachment: fixed;
}
.blog-details-area .title {
  margin-top: 30px;
  margin-bottom: 15px;
  font-weight: 500;
  font-size: 25px;
}

.blog-details {
  box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  background: var(--whiteColor);
}
.blog-details .article-img {
  position: relative;
}
.blog-details .article-img .date {
  position: absolute;
  bottom: 15px;
  width: 100px;
  left: 15px;
  height: 100px;
  text-align: center;
  background: var(--mainColor);
  color: var(--whiteColor);
  border-radius: 5px;
  padding-top: 19px;
  line-height: 32px;
  font-size: 25px;
  font-style: italic;
  font-weight: 500;
}
.blog-details .article-text {
  padding: 30px;
}
.blog-details .article-text ul li {
  display: inline-block;
}
.blog-details .article-text .category a {
  background: var(--mainColor);
  color: var(--whiteColor);
  padding: 3px 15px;
  display: inline-block;
  border-radius: 3px;
  font-weight: 400;
  margin-right: 10px;
}
.blog-details .article-text p {
  margin-bottom: 20px;
}
.blog-details .article-text .blockquote {
  background: #f4f4f4;
  padding: 30px;
  font-size: 17px;
  border-left: 4px solid var(--mainColor);
  margin-bottom: 25px;
  margin-top: 25px;
}
.blog-details .article-text .blockquote p {
  color: var(--blackColor);
}
.blog-details .article-text .author-share {
  margin-top: 40px;
}
.blog-details .article-text .author-share .social-share {
  text-align: right;
}
.blog-details .article-text .author-share .social-share a {
  background: var(--mainColor);
  color: var(--whiteColor);
  width: 35px;
  display: inline-block;
  height: 35px;
  text-align: center;
  line-height: 35px;
  border-radius: 50%;
  margin-left: 5px;
}
.blog-details .article-text .author-share .social-share a:hover {
  background-color: var(--blackColor);
  color: var(--whiteColor);
}
.blog-details .article-text .article-author {
  overflow: hidden;
}
.blog-details .article-text .article-author .author-img {
  float: left;
  width: 27%;
}
.blog-details .article-text .article-author .author-img img {
  border-radius: 50%;
  max-width: 100%;
  width: auto !important;
}
.blog-details .article-text .article-author .author-title {
  float: left;
  width: 73%;
  padding-left: 25px;
  margin-top: 17px;
}
.blog-details .article-text .article-author .author-title h4 {
  margin-bottom: 6px;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 500;
}
.blog-details .article-text .article-author .author-title span {
  color: #666666;
  font-size: 15px;
}

.post-controls-buttons {
  margin-top: 50px;
  margin-bottom: 50px;
  padding-left: 70px;
  padding-right: 70px;
}
.post-controls-buttons div {
  display: inline-block;
}
.post-controls-buttons div a {
  box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  padding: 10px 20px;
  border-radius: 5px;
  background: var(--whiteColor);
  color: var(--mainColor);
  display: inline-block;
}
.post-controls-buttons div a:hover {
  background: var(--mainColor);
  color: var(--whiteColor);
}
.post-controls-buttons .controls-right {
  float: right;
}

.post-comments .title {
  margin-bottom: 30px;
}
.post-comments .single-comment {
  box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  padding: 25px;
  margin-bottom: 25px;
  position: relative;
  overflow: hidden;
  background: var(--whiteColor);
}
.post-comments .single-comment.left-m {
  margin-left: 30px;
}
.post-comments .single-comment .comment-img {
  float: left;
  width: 13%;
}
.post-comments .single-comment .comment-img img {
  border-radius: 50%;
}
.post-comments .single-comment .comment-content {
  float: left;
  width: 87%;
  padding-left: 25px;
}
.post-comments .single-comment .comment-content h4 {
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 20px;
}
.post-comments .single-comment .comment-content p {
  margin-bottom: 15px;
}
.post-comments .single-comment .comment-content a {
  position: absolute;
  right: 25px;
  bottom: 25px;
  color: var(--mainColor);
  text-transform: uppercase;
  font-size: 17px;
}

.leave-a-reply {
  box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  padding: 25px;
  background: var(--whiteColor);
  margin-top: 50px;
}
.leave-a-reply .title {
  margin-bottom: 25px;
}
.leave-a-reply .btn {
  margin-top: 0px;
}

/*==============================
Footer Area CSS
==============================*/
.footer-area {
  text-align: center;
  padding: 50px 0;
  background: radial-gradient(ellipse at bottom, #080b0f 0%, #000000 100%) !important;
}
.footer-area h3 {
  line-height: 22px;
  margin-bottom: 30px;
}
.footer-area h3 a {
  color: var(--whiteColor);
  text-transform: uppercase;
  display: inline-block;
  font-size: 35px;
  font-weight: 600;
}
.footer-area h3 span {
  text-transform: lowercase;
  color: var(--mainColor);
  font-size: 35px;
  font-weight: 700;
}
.footer-area ul li {
  display: inline-block;
  margin-left: 4px;
  margin-right: 4px;
}
.footer-area ul li a {
  width: 32px;
  height: 32px;
  text-align: center;
  line-height: 31px;
  border: 1px solid var(--whiteColor);
  border-radius: 50%;
  color: var(--whiteColor);
  font-size: 14px;
  display: block;
}
.footer-area ul li a:hover, .footer-area ul li a:focus {
  background: var(--mainColor);
  color: var(--whiteColor);
  border-color: var(--mainColor);
}
.footer-area p {
  line-height: 1;
  margin-top: 30px;
  color: var(--whiteColor);
}

/*==============================
Sidebar Eaxmple Demo CSS
==============================*/
.demo-modal-panel {
  position: fixed;
  right: 0;
  top: 40%;
  transform: translateY(-40%);
  z-index: 99998;
}
.demo-modal-panel .sidebar-demo-control {
  display: inline-block;
  border: none;
  background-color: var(--blackColor);
  color: var(--whiteColor);
  padding: 0 0;
  letter-spacing: 1px;
  box-shadow: rgba(0, 0, 0, 0.15) 0 0 25px;
  padding: 12px 0 10px;
  transition: var(--transition);
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 5px 0 0 5px;
  font-weight: 600;
  font-size: 13px;
}
.demo-modal-panel .sidebar-demo-control span {
  writing-mode: vertical-rl;
  text-orientation: upright;
  padding-left: 5px;
  padding-right: 5px;
}
.demo-modal-panel .sidebar-demo-control:hover {
  color: var(--whiteColor);
  background-color: var(--mainColor);
}

.example-demo-modal {
  position: fixed;
  overflow: hidden;
  right: 0;
  top: 0;
  z-index: 99999;
  transition: 0.9s;
  opacity: 0;
  visibility: hidden;
  border-left: 1px solid #eeeeee;
  right: -100%;
}
.example-demo-modal .inner {
  width: 450px;
  height: 100vh;
  overflow-y: scroll;
  background-color: var(--whiteColor);
  box-shadow: 0 0 30px 0 rgba(67, 67, 67, 0.15);
  padding: 90px 30px 30px 30px;
}
.example-demo-modal .inner ul {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
}
.example-demo-modal .inner ul li {
  flex: 0 0 50%;
  max-width: 50%;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
}
.example-demo-modal .inner ul li .single-demo {
  position: relative;
  text-align: center;
  border-radius: 5px;
}
.example-demo-modal .inner ul li .single-demo img {
  border-radius: 5px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border: 5px solid var(--whiteColor);
}
.example-demo-modal .inner ul li .single-demo::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--mainColor);
  transition: var(--transition);
  opacity: 0;
  visibility: hidden;
  border-radius: 5px;
}
.example-demo-modal .inner ul li .single-demo span {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  color: var(--whiteColor);
  text-transform: capitalize;
  transition: var(--transition);
  opacity: 0;
  visibility: hidden;
  margin-top: 15px;
  font-size: 16px;
  font-weight: 600;
}
.example-demo-modal .inner ul li .single-demo .link-btn {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 3;
}
.example-demo-modal .inner ul li .single-demo:hover::before {
  opacity: 1;
  visibility: visible;
}
.example-demo-modal .inner ul li .single-demo:hover span {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
}
.example-demo-modal .header-title {
  position: absolute;
  background-color: var(--whiteColor);
  top: -1px;
  left: 0;
  right: 0;
  z-index: 5;
  border-bottom: 1px solid #eeeeee;
  padding-top: 18px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
}
.example-demo-modal .header-title .example-demo-modal-control {
  position: absolute;
  right: 25px;
  top: 15px;
  font-size: 20px;
  color: var(--blackColor);
  transition: var(--transition);
  background-color: transparent;
  border: none;
  padding: 0;
  display: inline-block;
}
.example-demo-modal .header-title .example-demo-modal-control:hover {
  color: var(--mainColor);
}
.example-demo-modal .header-title .title {
  color: var(--blackColor);
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 600;
}
.example-demo-modal.active {
  right: 0;
  opacity: 1;
  visibility: visible;
}

.video-popup .overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9991;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
}
.video-popup .overlay iframe {
  width: 600px;
  height: 520px;
}
.video-popup .overlay .nsm-content {
  top: 50%;
  left: 50%;
  width: 600px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  background-color: var(--whiteColor);
}
.video-popup .nsm-dialog-btn-close {
  background-color: transparent;
  color: var(--whiteColor);
  position: absolute;
  border: none;
  right: -30px;
  top: -30px;
}